*{
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}
.header{
  text-align: center;
  font-size: 24px;
  font-weight: bold;
  padding: 15px;
  color: black;
}
.navBar{
  width: 100%;
  height: 10vh;
  background: black;
  color: white;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
}

a {
  color: white;
  text-decoration: none;
}

.footer{
  width: 100%;
  height: 10vh;
  background: black;
  color: white;
  display: flex;
  align-items: center;
  justify-content: center;
}
.search-input {
  width: 40%;
  padding: 12px;
  border: 2px solid #55c1c6;
  border-radius: 4px;
  font-size: 16px;
  background-color: #f0f0f0;
  color: #333;
  outline: none;
  transition: border-color 0.3s ease-in-out;
  margin: 1rem;
}
.search-bar {
  display: flex;
  justify-content: center;
}
.search-input:focus {
  border-color: #257b8f;
}
.no-movie {
  font-size: 24px;
  color: black;
  font-weight: bold;
  text-align: center;
}
.favorites {
  width: 90%;
  margin: 1rem auto;
  padding: 1rem;
  outline: 1px solid #55c1c6;
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  gap: 1rem;
  justify-items: center;
  border-radius: 10px;
}
.MovieList {
  width: 90%;
  margin: 1rem auto;
  padding: 1rem;
  outline: 1px solid #55c1c6;
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  gap: 1rem;
  justify-items: center;
  border-radius: 10px;
}

.MovieList .movie {
  width: 100%;
  border: 1px solid #257b8f;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  color: #fff;
  padding: 1rem;
  background-color: rgba(0, 0, 0, 0.8);
  transition: transform 0.2s ease-in-out;
  cursor: pointer;
  overflow: hidden;
  margin-bottom: 10px;
  gap: 1rem;
}

.MovieList .movie:hover {
  transform: scale(1.05);
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.2); /* Add shadow on hover */
}

.MovieList .movie img {
  width: 100%;
  max-height: 20rem;
  object-fit: cover;
  border-radius: 10px;
  margin-bottom: 1rem;
}

.MovieList .movie h2 {
  font-size: 1.5rem;
  margin-bottom: 0.5rem;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.6); /* Add text shadow */
}

.MovieList .movie p {
  font-size: 1rem;
  line-height: 1.3;
  flex-grow: 1;
  overflow: hidden;
  text-overflow: ellipsis;
}

.movie-details{
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.MovieList .movie {
  animation: fadeIn 0.5s ease-in-out;
}

.add-to-fav {
  background-color: white;
  border-radius: 5px;
  color: black;
  font-weight: bolder;
  padding: 0.1rem;
  font-size: 20px;
  padding: 10px 20px
}
.added-to-fav{
  background-color: grey;
  border-radius: 5px;
  color: white;
  font-weight: bolder;
  padding: 0.1rem;
  font-size: 20px;
  padding: 10px 20px
}
.button-container {
  align-self: center;
  margin-top: auto;
}
